/* Partials imports */
/* Typography */
h1 {
  font-size: calc(12px + 4vw);
}

@media (min-width: 800px) {
  h1 {
    font-size: 2.488em;
  }
}

h2 {
  font-size: 1.215em;
  margin-bottom: 22px;
}

@media (min-width: 800px) {
  h2 {
    font-size: 1.383em;
  }
}

h3 {
  font-size: 1.067em;
}

@media (min-width: 800px) {
  h3 {
    font-size: 1.139em;
  }
}

.plus {
  color: #00b0ff;
}

@media (min-width: 800px) {
  .about-text {
    font-size: 18px;
  }
}

.language {
  color: #777777;
  margin: 0;
}

.link {
  font-weight: 700;
}

/* Card */
.card {
  box-shadow: 0px 2px 2px 0px rgba(0, 0, 0, 0.15);
  background-color: #fff;
  border-radius: 2px;
  margin: 0 0 30px 0;
}

.image-box {
  position: relative;
}

.image {
  width: 100%;
  max-width: 100%;
  border-radius: 2px 2px 0 0;
}

.content {
  padding: 15px;
}

@media (min-width: 768px) {
  .overlay {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    height: 100%;
    width: 100%;
    background-color: #263238;
    opacity: 0.4;
    transition: .5s;
    cursor: pointer;
  }
  .overlay:hover {
    opacity: 0;
  }
}

@media (min-width: 768px) {
  .card-stack {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
  }
}

@media (min-width: 768px) {
  .card-stack .col-sm-6 {
    display: flex;
    width: 50%;
  }
}

/* Layout imports */
/* Navigation */
nav {
  min-height: 50px;
  border-bottom: 1px solid rgba(0, 0, 0, 0.15);
  background-color: #fff;
}

.logo-link,
.nav-link {
  padding: 1em 1em 1em 0;
}

@media (min-width: 768px) {
  .logo-link,
  .nav-link {
    padding: 1em 2.5em 1em 0;
  }
}

.logo-img {
  height: 50px;
  margin: 10px 0 10px 0;
  display: inline-block;
}

.nav-list {
  list-style-type: none;
  margin: 0;
  padding: 0;
  display: inline;
}

.nav-item {
  display: inline-block;
  font-weight: bold;
}

.nav-link {
  min-height: 48px;
  min-width: 48px;
}

/* Additional section structure */
main {
  padding: 50px 0 0;
}

.vertical-offset {
  margin-bottom: 50px;
}

.banner-padding {
  padding: 50px 0;
}

/* Footer */
footer {
  border-top: 1px solid rgba(0, 0, 0, 0.15);
  width: 100%;
}

.footer-content {
  padding: 20px 0;
}

.contact-text {
  margin-bottom: 0;
}
